import React from "react";
import Card from "react-bootstrap/Card";

const API2 = "http://35.245.92.133:3001/photo/";

class Image extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ref: "",
      url: "",
      found: false
    };
    if (props.reff != null) {
      this.state.ref = props["reff"][0]["photo_reference"];
      fetch(API2 + this.state.ref)
        .then(response => response.json())
        .then(data => {
          this.setState({ url: data.ur, found: true });
        });
    }
  }
  render() {
    if (this.state.found === true) {
      return <Card.Img variant="top" src={this.state.url} />;
    } else {
      return <img />;
    }
  }
}

export default Image;
