import React from "react";
import Image from "./Image";
import Card from "react-bootstrap/Card";
import { CardColumns } from "react-bootstrap";
import "./Result.css";

const API1 = "http://35.245.92.133:3001/place/";
const API2 = "http://127.0.0.1:3001/zip/";

class Results extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      dat: [],
      cords: props.zip
    };
  }

  async componentDidMount() {
    fetch(API1 + this.state.cords.lat + "/" + this.state.cords.lng)
      .then(response => response.json())
      .then(data => {
        this.setState({ dat: data });
      });
  }
  render() {
    const { dat } = this.state;
    return (
      <CardColumns className="margin">
        {dat.map(hit => (
          <Card key={hit.id} className="shadow">
            <Image reff={hit.photos} />
            <Card.Body>
              <Card.Title>{hit.name}</Card.Title>
              <Card.Text>Ratting: {hit.rating}</Card.Text>
            </Card.Body>
          </Card>
        ))}
      </CardColumns>
    );
  }
}

export default Results;
